import React from 'react';

import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from "@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner";
import Alert from "@vkontakte/vkui/dist/components/Alert/Alert";

import '@vkontakte/vkui/dist/vkui.css';

import bridge from "@vkontakte/vk-bridge";

import CreateCardPanel from './panels/CreateCardPanel';
import CardInfoPanel from './panels/CardInfoPanel';
import ContactsPanel from './panels/ContactsPanel';

import { load } from './utils';
import { ConfigProvider } from '@vkontakte/vkui';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: "CardInfoPanel",
			popout: null,
			modal: null,

			scheme: 'bright_light',
			history: ["CardInfoPanel"],
			
			card: null,
		};
	}

	componentDidMount() {
		window.addEventListener("popstate", () => this.goBack());

		bridge.subscribe(({detail: {type, data}}) => {
			console.log("[vkb]:", type, data);

			if (type === "VKWebAppUpdateConfig") {
				this.setState(() => ({scheme: data.scheme ? data.scheme : 'bright_light'}));
			}
			
			if (type === "VKWebAppStorageGetResult") {
				let mobile = null;
				let cardId = null;
				for (let i = 0; i < 2; i++) {
					if (data.keys[i].key === "mobile") mobile = data.keys[i].value;
					else if (data.keys[i].key === "cardId") cardId = data.keys[i].value;
				}

				console.log("get storage", mobile, cardId);
				if (mobile && cardId) {
					this.updateCard(mobile, cardId);
				}
			}
		});

		bridge.send("VKWebAppStorageGet", {"keys": ["mobile", "cardId"]});
		//this.updateCard();
	}

	updateCard = (mobile, cardId) => {
		this.changePopout(<ScreenSpinner />);
		load(cardId, mobile)
			.then(card => {
				this.setState(() => ({card: card}));
				console.log(card);
				bridge.send("VKWebAppStorageSet", {"key": "mobile", "value": mobile});
				bridge.send("VKWebAppStorageSet", {"key": "cardId", "value": cardId});
				this.changePopout(null);
				this.goToPanel("CardInfoPanel");
			})
			.catch(err => {
				console.log(err);
				if (err === 404) {
					this.changePopout(<Alert actions={[{title: "Хорошо", autoclose: true}]} onClose={() => this.changePopout(null)}><h2>Карта не найдена</h2><p>Не удалось найти карту с данными номерами телефона и карты. Проверьте введённые данные и попробуйте ещё раз</p></Alert>);
				} else {
					this.changePopout(<Alert actions={[{title: "Попробовать ещё", autoclose: true}]} onClose={() => this.updateCard()}><h2>Ошибка подключения</h2><p>Не удалось получить ответ от сервера. Проверьте подключение к интернету и попробуйте ещё раз</p></Alert>);
				}
			})
	}

	changePanel = (panel) => {
		this.setState(() => ({activePanel: panel}));
	}

	changePopout = (popout) => {
		this.setState(() => ({popout: popout}));
	}

	changeModal = (modal) => {
		this.setState(() => ({modal: modal}));
	}

	goToPanel = (panel) => {
		window.history.pushState({panel: panel}, panel);

		this.setState((pv) => ({activePanel: panel, history: [...pv.history, panel]}));
	}

	goBack = () => {
		let history = this.state.history;

		if (history.length === 1) {
			bridge.send("VKWebAppClose", {"status": "success"});
		} else if (history.length > 1) {
			history.pop();
			this.setState(() => ({activePanel: history[history.length - 1]}));
		} 
	}

	render() {
		const {activePanel, popout, modal, scheme, card} = this.state;
	
		return (
			<ConfigProvider isWebView={true} scheme={scheme}>
				<View 
					activePanel={activePanel}
					history={this.state.history}
					onSwipeBack={this.goBack}
					popout={popout}
					modal={modal}>
					<CreateCardPanel id="CreateCardPanel" goToPanel={this.goToPanel} updateCard={this.updateCard} />
					<CardInfoPanel id="CardInfoPanel" goToPanel={this.goToPanel} changeModal={this.changeModal} card={card} />
					<ContactsPanel id="ContactsPanel" />
				</View>
			</ConfigProvider>
		)
	}
}

export default App;
