import React from 'react';
import PropTypes from 'prop-types';

import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import PanelHeaderContent from "@vkontakte/vkui/dist/components/PanelHeaderContent/PanelHeaderContent";
import PanelHeaderContext from "@vkontakte/vkui/dist/components/PanelHeaderContext/PanelHeaderContext";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import List from "@vkontakte/vkui/dist/components/List/List";
import Header from "@vkontakte/vkui/dist/components/Header/Header";
import SimpleCell from "@vkontakte/vkui/dist/components/SimpleCell/SimpleCell";
import ModalRoot from "@vkontakte/vkui/dist/components/ModalRoot/ModalRoot";
import ModalPage from "@vkontakte/vkui/dist/components/ModalPage/ModalPage";
import ModalPageHeader from "@vkontakte/vkui/dist/components/ModalPageHeader/ModalPageHeader";
import PanelHeaderButton from "@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton";
import InfoRow from "@vkontakte/vkui/dist/components/InfoRow/InfoRow";
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28MailOutline from '@vkontakte/icons/dist/28/mail_outline';
import Icon24Cancel from "@vkontakte/icons/dist/24/cancel";
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';

import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from "@vkontakte/vkui/dist/"

import CardInfoPlaceholder from "./../comps/CardInfoPlaceholder";

import { toShortCardId, getGWordSuffix, getMWordSuffix, formatAccessibleFor } from "./../utils";

class CardInfoPanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      contextOpened: false,
    };
  }



  toggleContext = () => {
    this.setState((pv) => ({contextOpened: !pv.contextOpened}));
  }

  generateModal = (minutes, expireDate, accessibleFor) => {
    let modal = 
      <ModalRoot activeModal="this">
        <ModalPage 
          id="this" 
          onClose={() => this.props.changeModal(null)}
          header={<ModalPageHeader
            left={IS_PLATFORM_ANDROID && <PanelHeaderButton onClick={() => this.props.changeModal(null)}><Icon24Cancel /></PanelHeaderButton>}
            right={IS_PLATFORM_IOS ? <PanelHeaderButton onClick={() => this.props.changeModal(null)}><Icon24Dismiss /></PanelHeaderButton> : null}
          >Абонемент</ModalPageHeader>}>
          
          <Group>
            <List>
              <SimpleCell>
                <InfoRow header="Количество минут">
                  {minutes + " минут" + getGWordSuffix(minutes)}
                </InfoRow>
              </SimpleCell>

              <SimpleCell>
                <InfoRow header="Действителен до">
                  {expireDate}
                </InfoRow>
              </SimpleCell>

              <SimpleCell>
                <InfoRow header="Доступность">
                  {formatAccessibleFor(accessibleFor)}
                </InfoRow>
              </SimpleCell>  
              <SimpleCell disabled></SimpleCell>  
            </List>
          </Group>
        </ModalPage>
      </ModalRoot>

    return modal;
  }

  render() {
    const {card} = this.props;

    if (card === null) {
      return (
        <Panel id="CardInfoPanel">
          <PanelHeader>Карта</PanelHeader>
          <CardInfoPlaceholder goToPanel={this.props.goToPanel} />
        </Panel>
      )
    }

    let smCells = [];
    for (let i = 0; i < card["SaleMinutes"].length; i++) {
      let sm = card["SaleMinutes"][i];
      let cell = <SimpleCell key={i} after={<Icon24Chevron />} onClick={() => this.props.changeModal(this.generateModal(sm["Minutes"], sm["ExpireDate"], sm["AccessibleFor"]))}>{sm["Minutes"] + " минут" + getGWordSuffix(sm["Minutes"])}</SimpleCell> 
      smCells.push(cell);
    }

    let pCells = [];
    for (let i = 0; i < card["Presents"].length; i++) {
      let p = card["Presents"][i];
      let cell = <SimpleCell key={i} description={"Действует до " + p["ExpireDate"]}>{p["Name"]}</SimpleCell>
      pCells.push(cell);
    }
      

    return (
      <Panel id="CardInfoPanel">
        <PanelHeader>
          <PanelHeaderContent 
            aside={<Icon16Dropdown style={{ transform: `rotate(${this.state.contextOpened ? '180deg' : '0'})` }} />}
            onClick={this.toggleContext}
          >
            {"Карта № " + toShortCardId(card["CardId"])}
          </PanelHeaderContent>
        </PanelHeader>
        <PanelHeaderContext opened={this.state.contextOpened} onClose={this.toggleContext}>
          <List>
            <SimpleCell before={<Icon28SettingsOutline />} onClick={() => this.props.goToPanel("CreateCardPanel")}>Изменить карту</SimpleCell>
            <SimpleCell before={<Icon28MailOutline />} onClick={() => this.props.goToPanel("ContactsPanel")}>Контакты</SimpleCell>
          </List>
        </PanelHeaderContext>
        

        <Group header={<Header mode="secondary">Основная информация</Header>}>
          <List>
            <SimpleCell description={card["SaleComment"]}>{"Скидка — " + card["Sale"] + " процент" + getMWordSuffix(card["Sale"])}</SimpleCell>
            <SimpleCell description="До 28.28.2288">{"Сумма на счету — " + card["Money"] + " руб."}</SimpleCell>
            <SimpleCell description={"Всего в солярии проведено " + card["TotalMinutes"] + " минут" + getGWordSuffix(card["TotalMinutes"])}>{"Накоплено " + 42 + " балл" + getMWordSuffix(42)}</SimpleCell>
          </List>
        </Group>

        <Group header={<Header mode="secondary">Абонементы</Header>}>
          <List>
            {smCells.length ? smCells : <SimpleCell>Абонементов пока что нет</SimpleCell>}
          </List>
        </Group>

        <Group header={<Header mode="secondary">Подарки</Header>}>
          <List>
            {pCells.length ? pCells : <SimpleCell>Подарков пока что нет</SimpleCell>}
          </List>
        </Group>

      </Panel>
    );
  }
}

CardInfoPanel.propTypes = {
  goToPanel: PropTypes.func.isRequired,
  changeModal: PropTypes.func.isRequired,
  card: PropTypes.object,
}

export default CardInfoPanel;
