import React from 'react';
import PropTypes from 'prop-types';

import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import FormLayout from "@vkontakte/vkui/dist/components/FormLayout/FormLayout";
import Input from "@vkontakte/vkui/dist/components/Input/Input";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import FormStatus from "@vkontakte/vkui/dist/components/FormStatus/FormStatus";
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";

import { formatCardId, trimMobile } from "./../utils"; 

class CreateCardPanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      inputCardId: "",
      inputMobile: "",

      errorMessage: null,
      errorHeader: null,
    };
  }

  onCardIdChanged = (e) => {
    let value = e.target.value;
    
    if (!this.isCardIdContainsOnlyDigits(value)) return;
    if (value.length > 32) return;

    this.setState(() => ({inputCardId: value, errorHeader: null}));
  }

  onMobileChanges = (e) => {
    let value = e.target.value;

    if (!this.isMobileContainsDigitsOrSpecialSymbols(value)) return;
    if (value.length > 32) return;

    this.setState(() => ({inputMobile: value, errorHeader: null}));
  }

  onAddClick = () => {
    const {inputCardId, inputMobile} = this.state;
    
    if (inputCardId.length === 0) {
      this.setState(() => ({errorHeader: "Некорректный номер карты", errorMessage: "Поле с номером карты должно быть заполнено"}));
      return;
    } else if (inputMobile.length === 0) {
      this.setState(() => ({errorHeader: "Некорректный номер телефона", errorMessage: "Поле с номером телефона должно быть заполнено"}));
      return;
    }

    let cardId = formatCardId(inputCardId);
    let mobile = trimMobile(inputMobile);
    this.props.updateCard(mobile, cardId);
    //this.props.goToPanel("CardInfoPanel");
  }

  isCardIdContainsOnlyDigits = (cardId) => /^\d*$/.test(cardId);

  isMobileContainsDigitsOrSpecialSymbols = (mobile) => /^[\d()+\- ]*$/.test(mobile)

  render() {
    const {inputCardId, inputMobile, errorHeader, errorMessage} = this.state;

    return (
      <Panel id="CreateCardPanel">
        <PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>Добавление карты</PanelHeader>
        <FormLayout>
          {errorHeader !== null ? <FormStatus header={errorHeader} mode="error">{errorMessage}</FormStatus> : null}
          <Input top="Номер карты" onChange={this.onCardIdChanged} value={inputCardId} placeholder="12345"/>
          <Input top="Номер телефона" onChange={this.onMobileChanges} value={inputMobile} placeholder="+79991234567"/>
          <Button size="xl" onClick={this.onAddClick}>Добавить карту</Button>
        </FormLayout>
      </Panel>
    );
  }
}

CreateCardPanel.propTypes = {
  goToPanel: PropTypes.func.isRequired,
  updateCard: PropTypes.func.isRequired,
}

export default CreateCardPanel;
